d black;\r\n  border-style: none;\r\n  border: 0.5px solid black;\r\n  color: black;\r\n  margin: 2px;\r\n}\r\n\r\n#full-desc {\r\n  padding: 5px;\r\n  border: none;\r\n  background-color: rgba(255, 255, 255, 0.8);\r\n  font-weight: bold;\r\n  border-bottom: 2px solid black;\r\n  font-family: var(--font-default);\r\n  cursor: pointer;\r\n\r\n  min-width: 44px;\r\n  min-height: 44px;\r\n}\r\n\r\n#full-desc:hover {\r\n  background-color: #bf1722;\r\n  color: white;\r\n  transition: ease-in 0.2s;\r\n}\r\n\r\n/* Detail Body */\r\n#body-detail {\r\n  margin-top: 10px;\r\n}\r\n\r\n.menu-list {\r\n  background-color: rgba(255, 255, 255, 0.8);\r\n  padding: 5px;\r\n  margin-bottom: 10px;\r\n  box-shadow: -1px 0px 4px 0px rgba(0, 0, 0, 0.75);\r\n}\r\n\r\n.menu-list > ul {\r\n  text-align: left;\r\n  padding: 5px;\r\n}\r\n\r\n.title-menu {\r\n  text-align: center;\r\n  background-color: #bf1722;\r\n  color: white;\r\n  padding: 10px;\r\n}\r\n/* Detail-footer */\r\n#footer-detail {\r\n  background-color: rgba(255, 255, 255, 0.8);\r\n  box-shadow: -1px 0px 4px 0px rgba(0, 0, 0, 0.75);\r\n  padding: 5px;\r\n}\r\n\r\n#footer-detail > h3 {\r\n  text-align: center;\r\n  background-color: yellow;\r\n  padding: 10px;\r\n}\r\n\r\n.customer-review {\r\n  padding: 10px;\r\n  border-bottom: 0.5px solid black;\r\n}\r\n\r\n.customer-review__header {\r\n  display: flex;\r\n  justify-content: space-between;\r\n}\r\n\r\n.customer-review:last-child {\r\n  border-bottom: none;\r\n}\r\n\r\n.customer-review > h4 {\r\n  width: fit-content;\r\n  padding: 4px;\r\n  color: black;\r\n  border-radius: 10px;\r\n}\r\n\r\n/* LIKE */\r\n.like {\r\n  width: 55px;\r\n  height: 55px;\r\n  display: flex;\r\n  align-items: center;\r\n  justify-content: center;\r\n  background-color: #db0000;\r\n\r\n  position: fixed;\r\n  bottom: 16px;\r\n  right: 16px;\r\n  border-radius: 50%;\r\n  border: 0;\r\n\r\n  font-size: 18px;\r\n  color: white;\r\n  cursor: pointer;\r\n}\r\n\r\n/* Footer */\r\nfooter {\r\n  background-color: #fa4659;\r\n  width: 100%;\r\n  bottom: 0;\r\n  color: white;\r\n  padding: 20px;\r\n  text-shadow: -1px 2px 5px 1px rgba(0, 0, 0, 0.75);\r\n}\r\nfooter > p {\r\n  text-align: center;\r\n  min-height: 44px;\r\n  min-width: 44px;\r\n}\r\n\r\nfooter > p > a {\r\n  text-decoration: none;\r\n  color: white;\r\n  font-weight: bolder;\r\n\r\n  min-height: 44px;\r\n  min-width: 44px;\r\n  display: inline-block;\r\n}\r\n"],"sourceRoot":""}]);
